// Core polyfills (loaded via "entry" mode)
import 'core-js/stable';
import 'regenerator-runtime/runtime';
import 'whatwg-fetch';
import 'intersection-observer';
import 'lazysizes';

// Explicit syntax-related polyfills for Safari 9
import 'core-js/features/array/includes';
import 'core-js/features/object/entries';
import 'core-js/features/object/values';
import 'core-js/features/promise'; // Ensures full Promise polyfill integration

// Force promise-polyfill if native is incomplete (Safari 9 has buggy native)
if (!window.Promise || !window.Promise.resolve) {
  import('promise-polyfill').then((module) => {
    window.Promise = module.default;
  });
}

// Object.assign polyfill
if (typeof Object.assign !== 'function') {
  Object.assign = function (target) {
    if (target == null) {
      throw new TypeError('Cannot convert undefined or null to object');
    }
    target = Object(target);
    for (var index = 1; index < arguments.length; index++) {
      var source = arguments[index];
      if (source != null) {
        for (var key in source) {
          if (Object.prototype.hasOwnProperty.call(source, key)) {
            target[key] = source[key];
          }
        }
      }
    }
    return target;
  };
}

// Global unhandled rejection handler for debugging
window.addEventListener('unhandledrejection', (event) => {
  console.error('Unhandled Promise Rejection:', event.reason);
  event.preventDefault(); // Prevent default to avoid crashes
});